
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#wrapper {
  --main-bg-color: #729ce9;
  --main-txt-color:  #33518b;
  background-color: var(--main-bg-color);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;  
}

#quote-box{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: var(--main-txt-color);
  border: 2px solid var(--main-bg-color);
  border-radius: 13%;
  width: 50vw;
  height: 50vh;
  text-align: center; 
  font-size: 100%;
  color: var(--main-bg-color);
}

#author {
  font-size: 3em;
  font-family: "Oxygen", sans-serif;
  text-shadow: 2px 2px rgb(1, 1, 46);
}

#text {
  font-size: 2.5em;
  font-family: "Fira Sans", sans-serif;
  margin: 0px 20px;
}

#buttons {
  display: flex;
  justify-content: space-around;
  margin-bottom: 4%;
}
button {
  background-color:var(--main-bg-color); 
  color: var(--main-txt-color);
  border: 2px solid var(--main-bg-color);
  border-radius: 15%;
  font-size: 2em;
  padding: 10px; /* Safari */
  transition-duration: 0.6s;
  cursor: pointer;
}
button:hover {
  background-color: white;
  color: black;
}

#new-quote {
  transition: all 0.5s;
}
#new-quote span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}
#new-quote span:after {
  content: '\BB';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

#new-quote:hover span {
  padding-right: 25px;
}

#new-quote:hover span:after {
  opacity: 1;
  right: 0;
}


